/**
 * @module model-bb-user-ng
 *
 * @description
 * Model for widget-bb-user-ng
 *
 * @example
 * import modelUserModuleKey, { modelUserKey } from 'model-bb-user-ng';
 *
 * angular
 *   .module('ExampleModule', [
 *     modelUserModuleKey,
 *   ])
 *   .factory('someFactory', [
 *     modelUserKey,
 *     // into
 *     function someFactory(userModel) {
 *       // ...
 *     },
 *   ]);
 */
import angular from 'vendor-bb-angular';

import userDataModuleKey, { userDataKey } from 'data-bb-user-http-ng';
import legalEntityDataModuleKey, { legalEntityDataKey } from 'data-bb-legalentity-http-ng';

import Model from './user';
import LegalEntitiesModel from './legal-entities';

const moduleKey = 'model-bb-user-ng';
const legalEntitiesModuleKey = 'model-bb-legal-entities-ng';
export const modelUserKey = `${moduleKey}:model`;
export const modelLegalEntitiesKey = `${legalEntitiesModuleKey}:model`;

export default angular
  .module(moduleKey, [
    userDataModuleKey,
    legalEntityDataModuleKey,
  ])

  .factory(modelLegalEntitiesKey, [
    '$q',
    legalEntityDataKey,
    /* into */
    LegalEntitiesModel,
  ])

  .factory(modelUserKey, [
    '$q',
    userDataKey,
    /* into */
    Model,
  ])

  .name;



